import React, {Component} from 'react';
import {Link} from 'react-router-dom';

import {Solutions} from '..';

class IndexSolutions extends Component {
	render() {
		return (
			<div id="fangan">
				<div className="container">
					<div className="row">
						<div className="page-title col-xs-12">
							<h1></h1>
							<div className="divider" />
						</div>
					</div>
					<div className="row">
						<div className="col-xs-12">
							<div className="fangan-list">
								<ul>
									<li></li>
									<li></li>
									<li></li>
									<li> </li>
									<li> </li>
									<li> </li>
								</ul>
							</div>
						</div>
					</div>
					<div className="row">
						<div className="col-xs-12">
							<div className="anli-more text-center">
								<Link to={Solutions.path} className="btn btn-default btn-lg"></Link>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}


export default IndexSolutions;



